.App-header {
  height: 50px;
  background-color: #32F8B1;
  display: flex;
  flex-direction: column;
}

.App-header a {
  text-decoration: none;
  color: black;
  margin: 5px;
}

.App-header .navbar {
  height: 100%;
  padding: 0;
}

.App-header .nav-link,
.App-header .navbar-nav {
  padding: 0;
  margin: 0;
  height: 45px;
}

.App-header .navbar img{
  height: 45px;
}

.App-header .navbar svg {
  height: 45px;
  width: 45px;
  cursor: pointer;
}

.App-header .dropdown-menu {
  display: none;
  padding: 20px;
  border-radius: 20px;
  background: rgba(241, 143, 59, 1);
  width: 60vw;
  max-width: 300px;
}

.App-header .navbar-expand .navbar-nav div.show {
  right: 0;
  left: auto;
  display: flex;
  flex-direction: column;
}

.App-header div.show svg{
  height: 20px;
  width: 20px;
}

.App-header .dropdown-menu a {
  margin: 5px auto 5px auto;
  font-size: 20px;
  color: rgba(78,121,165,1);
}

.App-header .dropdown-menu a:hover {
  color: black;
}

.App-header .dropdown {
  text-align: end;
}